import Helper from './_helper';

const options = {
    "type": "text",
    "id": "-",
    "pri_id": 0,
    "field_id" : 0 , "seq_no": 0 , 
    "title": "Text",
    "settings": [{
            "id": 0,
            "type": "textarea",
            "key": "text_content",
            "values": "",
            "label": ""
        }],
    "css": [{
            "id": 0,
            "type": "colorpicker",
            "key": "color",
            "values": "#595959",
            "label": ""
        },
        {
            "id": 0,
            "type": "select",
            "key": "font-size",
            "values": "14px",
            "label": ""
        },
        {
            "id": 0,
            "type": "select",
            "key": "text-align",
            "values": "center",
            "label": ""
        },
        {
            "id": 0,
            "type": "select",
            "key": "text-indent",
            "values": "0px",
            "label": ""
        }
    ],
    "parent_css": [{
            "id": 0,
            "type": "select",
            "key": "width",
            "values": "100%",
            "label": ""
        },
        {
            "id": 0,
            "type": "select",
            "key": "padding",
            "values": "10px",
            "label": ""
        }
    ],
    "label_css": []
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
    text_template(_options) {
        console.log(["text",_options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        return `
    <div style='float:left;${_parent_css}'>
        <p id='${_options.id}' style='word-break: break-all;margin:0px;${_css}'>${_options.settings[0].values}</p>
    </div>`;
    }
}


