import Helper from './_helper';

const options = {
    "type": "checkbox",
    "id": "-",
    "pri_id": 0,
    "field_id": 0,
    "seq_no": 0,
    "title": "Checkbox",
    "settings": [
        { "id": 0, "type": "text", "key": "text_title", "values": "Option 1", "label": "" },
        { "id": 0, "type": "select", "key": "selectfield", "values": "1", "label": "Field" }
    ],
    "css": [
        { "id": 0, "type": "colorpicker", "key": "border-color", "values": "#d9d9d9", "label": "" },
        { "id": 0, "type": "select", "key": "border-width", "values": "1px", "label": "" }
    ],
    "parent_css": [
        { "id": 0, "type": "select", "key": "width", "values": "100%", "label": "" },

        { "id": 0, "type": "select", "key": "padding", "values": "10px", "label": "" },
    ],
    "label_css": [
        { "id": 0, "type": "colorpicker", "key": "color", "values": "#595959", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-size", "values": "14px", "label": " Label" },
        { "id": 0, "type": "select", "key": "text-align", "values": "left", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-weight", "values": "normal", "label": " Label" }
    ]
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        options.title = _data.name;
        options.settings[0].values = _data.field_name;
        options.field_id = _data.id;
        return options;
    },
    chk_template(_options) {
        console.log(["checkbox", _options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        let _is_required = "",
            _is_required2 = "";
        if (_options.field.is_required == 1) {
            _is_required = "valid_textbox";
            _is_required2 = ` <span style="color:${$("#hid_dogjun_color").val()}">*</span>`;
        }
        //TODO:   is required 
        return `
    <div style='float:left;${_parent_css}'>
        <label class="mt-checkbox mt-checkbox-outline" style="${_label_css}">
            <input type="checkbox" name="field_items[${_options.field_id}]">${_options.settings[0].values}
            <span style="${_css}"></span>
        </label>
    </div>`;
    }
}


