//2.048
let _box = `
float: left;
height: 100%;
clear: none;
width: 100%;
background-color: rgba(0, 0, 0, 0.75);
min-width: 0px;
min-height: 0px;
position: fixed;
top:0px;
left: 0px;
z-index: 10;
display:none;`;

let _box1 = `
max-width: 90%;
float: none;
height: auto;
margin: 10% auto;
clear: none;
width: 412px;
background-color: #e06c22;
min-height: 40px;
padding: 20px;`;

let _box4 = `
float: none;
height: auto;
margin: 0px auto;
clear: both;
width: 80px;
border: 1px solid rgb(255, 255, 255);
border-radius: 8px !important;
cursor:pointer;
padding: 10px;`

let _text1 = `
float: left;
font-size: 1em;
width: 100%;
height: auto;
text-align: center;
font-weight: normal;
line-height: 1em;
margin: 0px;
clear: none;
min-height: 0px;
min-width: 0px;
color: rgb(255, 255, 255);`

let _box2 = `
float: none;
height: auto;
margin: 0px auto;
clear: none;
width: 100%;
min-width: 0px;`

let _text = `
float: left;
width: auto;
height: auto;
text-align: left;
font-weight: 100;
line-height: 30px;
margin: 0px;
clear: none;
min-height: 0px;`

let _box3 = `
float: left;
height: auto;
margin: 0px;
clear: none;
width: 100%;
min-width: 0px;
padding: 10px;`

const options = {
    "type": "fwd_popup", "id": "-", "pri_id": 0, "field_id" : 0 , "seq_no": 0 ,"title": "",
    "setting": [
        {"id": 0, "type": "textarea", "key": "text_content", "values": `    ("")          `, "label": ""}
    ],
    "css": [
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#16bae3", "label": " Label"},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": " Label"},
        {"id": 0, "type": "select", "key": "text-align", "values": "center", "label": " Label"}
        
    ],
    "parent_css": [
        {"id": 0, "type": "select", "key": "padding", "values": "10px", "label": ""},
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#FFFFFF", "label": " Popup"},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": " Popup"}
    ],
    "label_css": []
}

function convertCssItemEach(v2){
    let calc_pixel = 1;//2.26
    let arx = ["width", "height", "font-size"];
    let n = arx.indexOf(v2.key);
    let check_px = v2.values.split("px");
    let _result = "";
    if (check_px.length == 2) {
        _result += v2.key + ":" + (parseInt(check_px[0]) / calc_pixel) + "px;";
    } else {
        _result += v2.key + ":" + (v2.values) + ";";
    }
    return _result;
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
  hellosoda_popup_template : function (v) {
    console.log(v);
    return `
<div style="float:left;width:100%;${v.parent_css[0].key+":"+v.parent_css[0].values};">
    <p style="${v.css[2].key+":"+v.css[2].values};"><a href="javascript:$('#${v.id}_contraniner').show()" style="text-decoration: underline;${v.css[0].key+":"+v.css[0].values};${v.css[1].key+":"+v.css[1].values};">${v.title}</a></p>
    <div style="${_box}" class="clearfix" id="${v.id}_contraniner">
        <div style="${_box1}" class="clearfix">
            <div style="${_box2}" class="clearfix">
                <p style="${_text}${v.parent_css[1].key+":"+v.parent_css[1].values};${v.parent_css[2].key+":"+v.parent_css[2].values}" id="${v.id}">${v.settings[0].values}</p>
            </div>
            <div style="${_box3}" class="clearfix">
                <div style="${_box4}" class="clearfix" onclick="$('#${v.id}_contraniner').hide()">
                    <p style="${_text1}" ></p>
                </div>
            </div>
        </div>
    </div>
</div>
`;
  }
}



