import Helper from './_helper';

const options = {
    "type": "radio",
    "id": "-",
    "pri_id": 0,
    "field_id": 0,
    "seq_no": 0,
    "title": "Radio Button",
    "settings": [
        { "id": 0, "type": "textdis", "key": "text_title", "values": " - ", "label": "" }
    ],
    "css": [
        { "id": 0, "type": "colorpicker", "key": "color", "values": "#595959", "label": "" },
        { "id": 0, "type": "select", "key": "font-size", "values": "30px", "label": "" }
    ],
    "parent_css": [
        { "id": 0, "type": "select", "key": "width", "values": "100%", "label": "" },
        { "id": 0, "type": "select", "key": "padding", "values": "10px", "label": "" }
    ],
    "label_css": [
        { "id": 0, "type": "colorpicker", "key": "color", "values": "#595959", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-size", "values": "30px", "label": " Label" },
        { "id": 0, "type": "select", "key": "text-align", "values": "left", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-weight", "values": "normal", "label": " Label" }
    ],
    "data_set": []
}

export default {
    push_object(_new_id, _data) {
        options.id = _new_id;
        options.title = _data.name;
        options.settings[0].values = _data.field_name;
        options.field_id = _data.id;
        let new_options = [];
        _data.field_items.map((v, i) => {
            new_options.push({
                id: v.id,
                title: v.value
            });
        });
        options.data_set = new_options;
        return options;
    },
    radio_template(_options) {
        console.log(["radio", _options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        let str_builder = "";
        let _is_readonly = "";
        if (_options.field.is_readonly == 1 && _options.auto_fill != "") {
            _is_readonly = `disabled`;
        }

        _options.data_set.map((v, i) => {
            let _xchecked = "";
            if (_options.auto_fill != "") {
                if (v.title.toString() === _options.auto_fill) {
                    _xchecked = `checked="checked"`;
                }
            } else {
                if (i == 0) _xchecked = `checked="checked"`;
            }
            str_builder += `<label style="${_css}width: 100%;margin-bottom: 10px;margin-left:0px;" class="checkbox-inline"><input ${_xchecked} ${_is_readonly} type="radio" name="field_items[${_options.field_id}]" value="${v.title}"> ${v.title}</label>`;
        });
        let _is_required = "",
            _is_required2 = "";
        if (_options.field.is_required == 1) {
            _is_required = "valid_radio";
            _is_required2 = ` <span style="color:${$("#hid_dogjun_color").val()}">*</span>`;
        }

        return `
    <div style='float:left;${_parent_css}'>
        <label style='width:100%;${_label_css}'>${_options.settings[0].values}${_is_required2}</label>
        ${str_builder}
    </div>`;
    }
}


