import Helper from './_helper';

const options = {
    "type": "submit", "id": "-", "pri_id": 0, "field_id" : 0 , "seq_no": 0 , "title": "Submit Button",
    "settings": [
        {"id": 0, "type": "text", "key": "text_content", "values": "Save", "label": ""}
    ],
    "css": [
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#565656", "label": ""},
        {"id": 0, "type": "colorpicker", "key": "background-color", "values": "#ffd535", "label": ""},
        {"id": 0, "type": "colorpicker", "key": "border-color", "values": "#e4bf33", "label": ""},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": ""},
        {"id": 0, "type": "select", "key": "font-weight", "values": "normal", "label": ""},
        {"id": 0, "type": "select", "key": "padding", "values": "15px", "label": ""}
    ],
    "parent_css": [],
    "label_css": []
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
    submit_template(_options) {
        console.log(["submit",_options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        _options.settings[0].values = _options.settings[0].values===null?"":_options.settings[0].values;
        return `
    <div style='width:100%;;float:left;${_parent_css}'>
        <input type="submit" id='${_options.id}' style="border: none;cursor: pointer;margin: 0px;width: 100%;${_css}" value="${_options.settings[0].values}">
    </div>`;
    }
}


