import Helper from './_helper';

const options = {
    "type": "textlink", "id": "-", "pri_id": 0, "field_id" : 0 , "seq_no": 0 , "title": "Text Link",
    "settings": [
        {"id": 0, "type": "text", "key": "text_content", "values": "Click", "label": ""},
        {"id": 0, "type": "text", "key": "url_link", "values": "http://google.co.th", "label": "URL Address"}
    ],
    "css": [
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#1500ff", "label": ""},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": ""},

        {"id": 0, "type": "select", "key": "text-indent", "values": "0px", "label": ""}
    ],
    "parent_css": [
        {"id": 0, "type": "select", "key": "width", "values": "100%", "label": ""},
        {"id": 0, "type": "select", "key": "padding", "values": "10px", "label": ""},
        {"id": 0, "type": "select", "key": "text-align", "values": "left", "label": ""}
    ],
    "label_css": []
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
    textlink_template(_options) {
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        console.log(["textlink",_options]);
        return `<div style='float:left;${_parent_css}'>
        <a id='${_options.id}' style='margin:0px;${_css}' href='${_options.settings[1].values}' >${_options.settings[0].values}</a>
    </div>`;
    }
}


