import Helper from './_helper';

const options = {
    "type": "checkbox_agree_link",
    "id": "-",
    "pri_id": 0,
    "field_id": 0,
    "seq_no": 0,
    "title": "Checkbox Agree Link",
    "setting": [
        { "id": 0, "type": "textarea", "key": "text_title", "values": "Option 1", "label": "" },
        { "id": 0, "type": "text", "key": "url_link", "values": "http://google.co.th", "label": "URL Address" }
    ],
    "css": [
        { "id": 0, "type": "colorpicker", "key": "border-color", "values": "#ffffff", "label": "" },
        { "id": 0, "type": "select", "key": "border-width", "values": "1px", "label": "" },
        { "id": 0, "type": "select", "key": "border-radius", "values": "3px", "label": "" }
    ],
    "parent_css": [
        { "id": 0, "type": "select", "key": "width", "values": "100%", "label": "" },
        { "id": 0, "type": "select", "key": "padding-top", "values": "10px", "label": "()" },
        { "id": 0, "type": "select", "key": "padding-left", "values": "30px", "label": "()" },
        { "id": 0, "type": "select", "key": "padding-right", "values": "30px", "label": "()" },
        { "id": 0, "type": "select", "key": "padding-bottom", "values": "10px", "label": "()" }
    ],
    "label_css": [
        { "id": 0, "type": "colorpicker", "key": "color", "values": "#ffffff", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-size", "values": "18px", "label": " Label" },
        { "id": 0, "type": "select", "key": "text-align", "values": "left", "label": " Label" },
        { "id": 0, "type": "select", "key": "font-weight", "values": "normal", "label": " Label" }
    ]
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
    chk_agree_template(_options) {

        console.log(["checkbox-agree-link", _options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        let _href = _options.settings[1].values;
        let new_color = `${_options.label_css[0].key} : ${_options.label_css[0].values};`;

        return `
    <div style='float:left;${_parent_css}'>
        <label class="mt-checkbox mt-checkbox-outline" style="float:left;word-break: break-all;${_label_css}">
            <input class="chk_arg" type="checkbox"><a style="${new_color}" href="${_href}">${_options.settings[0].values}</a>
            <span style="${_css}"></span>
        </label>
    </div>`;
    }
}


