import Helper from './_helper';

const options = {
    "type": "images", "id": "-", "pri_id": 0, "field_id" : 0 , "seq_no": 0 , "title": "Images",
    "settings": [
        {"id": 0, "type": "textarea", "key": "image_src", "values": "https://daily.rabbitstatic.com/wp-content/uploads/2017/02/FWD-Life-Insurance-Logo.png", "label": "Images URL"}
    ],
    "css": [
        {"id": 0, "type": "text", "key": "width", "values": "100px", "label": ""},
        {"id": 0, "type": "text", "key": "height", "values": "60px", "label": ""}
    ],
    "parent_css": [
        {"id": 0, "type": "select", "key": "width", "values": "100%", "label": ""},
        {"id": 0, "type": "select", "key": "padding", "values": "10px", "label": ""},
        {"id": 0, "type": "select", "key": "text-align", "values": "center", "label": ""}
    ],
    "label_css": []
}

export default {
    push_object(_new_id) {
        options.id = _new_id;
        return options;
    },
    img_template(_options) {
        console.log(["images",_options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);

        return `
    <div style='float:left;width:100%;${_parent_css}'>
        <img src="${_options.settings[0].values}" style="${_css}" alt="">
    </div>`;
    }
}


