function convertCssItemEach(i2, v2){
    let arx = ["width", "height", "font-size"];
    let n = arx.indexOf(v2.key);
    let check_px = v2.values.split("px");
    let _result = "";
    if (check_px.length == 2) {
        _result += v2.key + ":" + parseInt(check_px[0]) + "px;";
    } else {
        _result += v2.key + ":" + (v2.values) + ";";
    }
    return _result;
}
export default {
    convertCss: function (v) {
        let _css = "";
        let _parent_css = "";
        let _label_css = "";

        v.css.map((v2,i2)=>{
            _css += convertCssItemEach(i2, v2);
        });
        v.parent_css.map((v2,i2)=>{
            _parent_css += convertCssItemEach(i2, v2);
        });
        
        v.label_css.map((v2,i2)=>{
            _label_css += convertCssItemEach(i2, v2);
        });

        return {
            _css,
            _parent_css,
            _label_css
        }
    }
}


